<template lang="pug">
  div(class="embassy-root")
    //- div(id="head")
    //-   div
    //-     img(src="@/assets/icon/yu-portrait.png")
    //-   div
    //-     h3 uid
    //-     h6 {{user.uid}}
    //- div
    //-   label(for="uid") 收尋玩家
    //-   input(type="number" id="uid" v-model="uid")
    //-   button(@click="search") 搜尋
    //- div(v-if="searchedUser")
    //-   p 玩家名稱 {{searchedUser.username}}
    //-   button(@click="submitAddFriend") 申請好友
    //- tab(
    //-   :navList="navList"
    //-   @switchMenu="switchMenu")
    //-   div(slot="content")
    //-     div(v-if="currentMenu === '隊伍'")
    //-       template(v-for="friend in user.friends")
    //-         h3 {{friend.username}}
    //-     div(v-if="currentMenu === '好友'")
    //-       template(v-for="friend in user.friendInvitations")
    //-         h3 {{friend.username}}
    //-         button(@click="ensureAcceptFriendInvitation(friend)") 接受
    tab(
      :navList="navList"
      @switchMenu="switchMenu")
      div(slot="content")
        embassy-detail(v-if="currentMenu === '隊伍'" :user="user" :currentString="'隊友'" :embassyData="team")
        embassy-detail(v-if="currentMenu === '好友'" :user="user" :currentString="'好友'" :embassyData="friend")
</template>

<script>
import Tab from '@/components/Tab.vue'
import { mapMutations, mapActions } from 'vuex'
import EmbassyDetail from '@/components/game/build/buildingFeature/EmbassyDetail.vue'

export default {
  components: {
    Tab,
    EmbassyDetail
  },
  props: {
    user: Object
  },
  data () {
    return {
      navList: ['隊伍', '好友'],
      currentMenu: '隊伍',
      uid: '',
      searchedUser: '',
      team: {
        inTeam: true,
        leader: true,
        memberNum: 0,
        buff: 10,
        member: [{ 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 },
          { 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 },
          { 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 },
          { 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 }
        ],
        invitation: [
          [{ 'name': '王小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 },
            { 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 },
            { 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 },
            { 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 }
          ],
          [{ 'name': '楊小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 },
            { 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 },
            { 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 },
            { 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 }
          ],
          [{ 'name': '盧小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 },
            { 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 },
            { 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 },
            { 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 }
          ]

        ],
        recommendMember: [{ 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 },
          { 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 },
          { 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 },
          { 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 }
        ]
      },
      friend: {
        member: [{ 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 },
          { 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 },
          { 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 },
          { 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 }
        ],
        invitation: [{ 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 },
          { 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 },
          { 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 },
          { 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 }
        ],
        recommendMember: [{ 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 },
          { 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 },
          { 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 },
          { 'name': '陳小名', 'title': '隊長', 'positionX': 1234, 'positionY': 2342 }
        ]
      }
    }
  },
  async created () {
    await this.getFriends()
    await this.getAllFriendInvitations()
  },
  sockets: {
    getAllFriendInvitations (user) {
      this.getAllFriendInvitations()
    },
    getFriends () {
      this.getFriends()
    },
    serverError (msg) {
      console.log(msg)
    }
  },
  methods: {
    ...mapMutations('feature', ['SHOW_NOTIFICATION']),
    ...mapActions('user', ['searchUser', 'getFriends', 'acceptFriendInvitation', 'getAllFriendInvitations', 'addFriend']),
    search () {
      this.searchUser({ uid: this.uid })
        .then(user => {
          this.searchedUser = user
        })
        .catch(error => {
          if (error.response.status === 404) {
            this.SHOW_NOTIFICATION('找不到此玩家')
          }
          if (error.response.status === 405) {
            this.SHOW_NOTIFICATION('此玩家尚未擁有外交大使館')
          }
        })
    },
    submitAddFriend () {
      this.$socket.emit('addFriend', {
        user: {
          uid: this.user.uid,
          username: this.user.username,
          nickname: this.user.username
        },
        friend: this.searchedUser
      })
    },
    ensureAcceptFriendInvitation (friend) {
      this.$socket.emit('acceptFriendInvitation', {
        user: {
          uid: this.user.uid,
          username: this.user.username,
          nickname: this.user.username
        },
        friend: friend
      })
      this.acceptFriendInvitation({ user: friend })
    },
    switchMenu (one) {
      this.currentMenu = one
    }
  }
}
</script>

