<template lang="pug">
div(class="list-root")
  div(v-for="(member,index) in members")
    div(v-if="buttonText==='加入' || buttonText==='邀請'" class="invitation-div")
      img(src="@/assets/icon/yu-portrait.png")
      div
        h5 {{member.name}}
        h5 距離3.0公里
      button {{buttonText}}
    div(v-else class="other-div")
      img(src="@/assets/icon/yu-portrait.png")
      div
        h5 {{member.name}}
        h5 距離30公里
      button {{buttonText}}
      button(v-if="type!=0") more
</template>
<script>
export default {
  props: {
    members: Array,
    buttonText: String
  },
  data: function () {
    return {
      distance: [],
      myPositionX: 1000,
      myPositionY: 2200
    }
  },
  created () {
    console.log(this.members)
    this.members.forEach(item => {
      var x = item.positionX - this.myPositionX
      var y = item.positionY - this.myPositionY
      var test = (Math.sqrt(Math.pow(x) + Math.pow(y)))
      console.log(test)
    })
  }
}
</script>
<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";
.list-root {
  display: grid;
  grid-auto-rows: 60px;
  grid-template-columns: 240px;
  grid-row-gap: 15px;
  justify-content: center;
  align-items: center;
  div {
    .invitation-div {
      grid-template-columns: 61px 1fr 45px;
      grid-template-areas: "picture person-content button";
      > button:nth-child(3) {
        font-size: 10px;
        height: 18px;
        grid-area: button;
        border: none;
        border-radius: 1px;
        background: map-get($linear-gradient, primary);
        color: map-get($colors, white);
        font-weight: 700;
      }
    }
    .other-div {
      grid-template-columns: 61px 1fr auto 8px 30px;
      grid-template-areas: "picture person-content button . button-more";
      > button:nth-child(3) {
        padding-left: 10px;
        padding-right: 10px;
        font-size: 10px;
        height: 18px;
        grid-area: button;
        border-color: map-get($colors, primary);
        border-radius: 1px;
        background-color: transparent;
        color: map-get($colors, white);
        font-weight: 700;
      }
      > button:nth-child(4) {
        font-size: 10px;
        height: 18px;
        grid-area: button-more;
        border-color: map-get($colors, primary);
        border-radius: 1px;
        background-color: transparent;
        color: map-get($colors, white);
        font-weight: 700;
      }
    }
    > div:nth-child(1) {
      display: grid;
      align-items: center;
      justify-content: center;
      > img {
        grid-area: picture;
        width: 45px;
        height: 45px;
        background-size: contain;
        border-radius: 50%;
        border: 1px solid map-get($colors, white);
        box-shadow: 0 0 4px map-get($colors, white);
      }
      > div {
        grid-area: person-content;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        > h5 {
          text-align: left;
          font-size: 12px;
        }
      }
    }
  }
}
</style>>
