<template lang="pug">
  div(class="team-root")
    div
      div(v-if="state===0" class = "no-team-no-invitation")
        span 快和{{currentString}}們一起採集獲得更多建築資源吧！
      div(v-else-if="state===1" class = "no-team-have-invitation")
        div(class="heading-default")
          h4 {{currentString}}邀請
        member-list(:members="embassyData.invitation" :buttonText="topButtonText" class="member-list")
      div(v-else class="have-team")
        div
          img
          div
            h5 組隊效果
            h5 - 採集能力提升10%
        member-list(:members="embassyData.member" :buttonText="topButtonText" class="member-list")
        div(class="invitation-add-div")
          div
            h1 +
          div
            h5 新增隊員
    div(v-if="state!==2" :type=0 class="team-input")
      input(type="text" placeholder="ID")
      button(class="button-default") {{InputButtonText}}
    div(class="team-list")
      div(class="heading-default")
        h4 {{currentString}}建議
      member-list(:members="embassyData.recommendMember" :buttonText="bottomButtonText" class="member-list")
    div(v-if="haveFriend" class="friend-list")
      div(class="heading-default")
        h4 好友名單
      member-list(:members="embassyData.member" :buttonText="'邀請組隊'" class="member-list")
</template>
<script>
import MemberList from '@/components/MemberList.vue'
export default {
  props: {
    currentString: String,
    user: Object,
    embassyData: Object
  },
  components: {
    MemberList
  },
  data: function () {
    return {
      // topButtonText: ''
      // state: 2,
      // buttonText: String,
      // no team no invitation for 0, no team have invitation for 1, have team for 2
    }
  },
  computed: {
    InputButtonText: function () {
      return (this.currentString === '隊友') ? '邀請組隊' : '加入好友'
    },
    state: function () {
      if (this.currentString === '隊友') {
        if (this.embassyData.length === 0) return 1
        return 2
      } else {
        if (this.embassyData.invitation.length !== 0) return 1
        return 0
      }
    },
    bottomButtonText: function () {
      return (this.currentString === '隊友') ? '邀請' : '加入'
    },
    topButtonText: function () {
      if (this.currentString === '隊友') {
        if (this.state === 1) return '邀請'
        return '位置'
      } else {
        return '加入'
        // '邀請組隊'
      }
    },
    haveFriend: function () {
      if (this.currentString === '好友' && this.embassyData.member.length !== 0) {
        return true
      } else {
        return false
      }
    }
  }
}
</script>
<style lang="scss">
@import "@/assets/scss/variables.scss";
.team-root {
  overflow-y: scroll;
  display: grid;
  // justify-content: center;
  // align-items: center;
  grid-template-rows: auto auto auto auto;
  grid-template-areas:
    "team-invitation"
    "input"
    "team-list"
    "friend-list";
  > div:nth-child(1) {
    grid-area: team-invitation;
    display: grid;
    grid-template-rows: auto;
    grid-template-areas: "invitation";
    .no-team-no-invitation {
      grid-area: invitation;
      width: 137px;
      height: 30px;
      justify-self: center;
      > span {
        font-size: 13px;
      }
    }
    .no-team-have-invitation {
      grid-area: invitation;
      display: grid;
      grid-template-rows: 23px 10px auto;
      grid-template-areas:
        "title"
        "."
        "person-list";
      > div:nth-child(1) {
        grid-area: title;
      }
      .member-list {
        grid-area: person-list;
      }
    }
    .have-team {
      grid-area: invitation;
      display: grid;
      grid-template-rows: 60px 23px auto 3px 60px 20px;
      grid-template-areas: "div" "." "list-member" "." "add-div" ".";
      > div:nth-child(1) {
        grid-area: div;
        width: 200px;
        height: 60px;
        border-radius: 4px;
        border: 1px solid map-get($colors, primary);
        box-shadow: 0 0 4px map-get($colors, primary);
        display: grid;
        grid-template-columns: 20px 40px 10px 1fr;
        grid-template-areas: ". pic . text";
        justify-self: center;
        justify-content: center;
        align-items: center;
        > img {
          grid-area: pic;
          width: 40px;
          height: 40px;
        }
        > div {
          grid-area: text;
          display: flex;
          flex-direction: column;
          justify-content: flex-start;
          > h5 {
            font-size: 12px;
            text-align: left;
          }
        }
      }
      .member-list {
        grid-area: list-member;
      }
      .invitation-add-div {
        padding-top: 15px;
        padding-bottom: 20px;
        grid-area: add-div;
        display: grid;
        grid-template-columns: 60px 180px;
        grid-template-areas: "picture person-content button";
        align-items: center;
        justify-content: center;
        justify-self: center;
        > div:nth-child(1) {
          grid-area: picture;
          width: 45px;
          height: 45px;
          background-size: contain;
          border-radius: 50%;
          border: 1px solid map-get($colors, white);
          box-shadow: 0 0 4px map-get($colors, white);
          > h1 {
            line-height: 47px;
          }
        }
        > div:nth-child(2) {
          grid-area: person-content;
          display: flex;
          flex-direction: column;
          justify-content: flex-start;
          > h5 {
            text-align: left;
            font-size: 12px;
          }
        }
      }
    }
  }
  .team-input {
    padding-top: 23px;
    padding-bottom: 21px;
    grid-area: input;
    display: grid;
    align-items: center;
    grid-template-rows: 50px 17px 32px;
    grid-template-areas:
      "placeholder"
      "."
      "button";
    > input {
      grid-area: placeholder;
      border: 1px solid map-get($colors, white);
      border-radius: 7.5px;
      background-color: rgba($color: map-get($colors, black), $alpha: 0.7);
      box-shadow: map-get($shadow, secondary-4);
      width: 277px;
      height: 50px;
    }
    > button {
      grid-area: button;
      justify-self: center;
      width: 116px;
      height: 32px;
    }
  }
  .team-list {
    grid-area: team-list;
    display: grid;
    grid-template-rows: 23px 10px 1fr;
    grid-template-areas:
      "title"
      "."
      "person-list";
    > div:nth-child(1) {
      grid-area: title;
    }
    .member-list {
      grid-area: person-list;
    }
  }
  .friend-list {
    padding-top: 20px;
    grid-area: friend-list;
    display: grid;
    grid-template-rows: 23px 10px 1fr;
    grid-template-areas:
      "title"
      "."
      "person-list";
    > div:nth-child(1) {
      grid-area: title;
    }
    .member-list {
      grid-area: person-list;
    }
  }
}
</style>
